.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
}

.loader {
    display: none;
    margin: 20px auto;
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #3498db;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.breed-select {
    width: 50%;
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 10px;
    color: darkblue;
}

.cat-info {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.cat-info img {
    max-width: 50%;
}

.error {
    display: none;
    color: red;
    font-weight: bold;
    margin-top: 20px;
}