.container {
  max-width: 600px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

.loader {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 20px auto;
  animation: spin 1s linear infinite;
  display: none;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  width: 50%;
  color: #00008b;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
}

.cat-info {
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.cat-info img {
  max-width: 50%;
}

.error {
  color: red;
  margin-top: 20px;
  font-weight: bold;
  display: none;
}

